import React, { Fragment } from "react";
import { Label, Input } from "reactstrap";
import styles from "./styles.module.scss";

const PriceFilter = () => {
    const options = [
        "Tt c mc gi",
        "Di 15 triu",
        "T 15 - 20 triu",
        "T 20 - 25 triu",
        "Trn 25 triu",
    ];

    return (
        <Fragment>
            <b>Mc gi</b>
            <br />
            {options.map((option, index) => {
                return (
                    <Fragment>
                        <Label>
                            <Input
                                type="radio"
                                className={styles.radio}
                                defaultChecked={index === 0}
                                name="price"
                                value={index}
                            />
                            {option}
                        </Label>
                        <br />
                    </Fragment>
                );
            })}
        </Fragment>
    );
};

export default PriceFilter;
